import axios from 'axios';

const API_URL = 'http://localhost:5000/api/v1';

// Get all topics with subtopics
export const getTopicsWithSubtopics = async () => {
  try {
    const response = await axios.get(`${API_URL}/topics/all`);
    return response.data.data;
  } catch (error) {
    console.error('Error fetching topics:', error);
    throw error;
  }
};

// Get subtopic by ID
export const getSubtopicById = async (id) => {
  try {
    const response = await axios.get(`${API_URL}/subtopics/${id}`);
    return response.data.data;
  } catch (error) {
    console.error('Error fetching subtopic:', error);
    throw error;
  }
};
