"use client";
import * as React from "react";
import { useState, useEffect } from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import CssBaseline from "@mui/material/CssBaseline";
import Divider from "@mui/material/Divider";
import Drawer from "@mui/material/Drawer";
import IconButton from "@mui/material/IconButton";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import CodeIcon from "@mui/icons-material/Code";
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";
import Collapse from "@mui/material/Collapse";
import { getTopicsWithSubtopics, getSubtopicById } from "@/utils/api";
import api from "@/apiHandler/page";

const drawerWidth = 240;

function ResponsiveDrawer({ window }) {
  const [mobileOpen, setMobileOpen] = useState(false);
  const [isClosing, setIsClosing] = useState(false);
  const [topics, setTopics] = useState([]);
  const [selectedSubtopic, setSelectedSubtopic] = useState(null);
  const [expandedTopics, setExpandedTopics] = useState({});
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const handleDrawerClose = () => {
    setIsClosing(true);
    setMobileOpen(false);
  };

  const handleDrawerTransitionEnd = () => {
    setIsClosing(false);
  };

  const handleDrawerToggle = () => {
    if (!isClosing) {
      setMobileOpen(!mobileOpen);
    }
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await api.get("/topics/all");
        setTopics(data.data);
        // Initialize expanded state for each topic
        const initialExpanded = {};
        data.data.forEach((topic) => {
          initialExpanded[topic._id] = false;
        });
        setExpandedTopics(initialExpanded);
      } catch (err) {
        setError("Failed to fetch topics");
        console.error(err);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const handleTopicClick = (topicId) => {
    setExpandedTopics((prev) => ({
      ...prev,
      [topicId]: !prev[topicId],
    }));
  };

  const handleSubtopicClick = async (subtopicId) => {
    try {
      const data = await api.get(`/subtopics/${subtopicId}`);
      setSelectedSubtopic(data.data);
    } catch (err) {
      console.error("Error fetching subtopic details:", err);
    }
  };

  const drawer = (
    <div>
      <Toolbar />
      <Divider />
      <List>
        {loading ? (
          <Typography>Loading...</Typography>
        ) : error ? (
          <Typography color="error">{error}</Typography>
        ) : (
          topics.map((topic) => (
            <div key={topic._id}>
              <ListItemButton onClick={() => handleTopicClick(topic._id)}>
                <ListItemIcon>
                  <CodeIcon />
                </ListItemIcon>
                <ListItemText primary={topic.name} />
                {expandedTopics[topic._id] ? <ExpandLess /> : <ExpandMore />}
              </ListItemButton>
              <Collapse
                in={expandedTopics[topic._id]}
                timeout="auto"
                unmountOnExit
              >
                <List component="div" disablePadding>
                  {topic.subtopics.map((subtopic) => (
                    <ListItemButton
                      key={subtopic._id}
                      sx={{ pl: 4 }}
                      onClick={() => handleSubtopicClick(subtopic._id)}
                    >
                      <ListItemText primary={subtopic.name} />
                    </ListItemButton>
                  ))}
                </List>
              </Collapse>
            </div>
          ))
        )}
      </List>
    </div>
  );

  // Remove this const when copying and pasting into your project.
  const container =
    window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar
        position="fixed"
        sx={{
          width: { sm: `calc(100% - ${drawerWidth}px)` },
          ml: { sm: `${drawerWidth}px` },
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: "none" } }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div">
            Responsive drawer
          </Typography>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        aria-label="mailbox folders"
      >
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onTransitionEnd={handleDrawerTransitionEnd}
          onClose={handleDrawerClose}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
          slotProps={{
            root: {
              keepMounted: true, // Better open performance on mobile.
            },
          }}
        >
          {drawer}
        </Drawer>
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: "none", sm: "block" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: drawerWidth,
            },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          width: { sm: `calc(100% - ${drawerWidth}px)` },
        }}
      >
        <Toolbar />
        {selectedSubtopic ? (
          <Box>
            <Typography variant="h4" gutterBottom>
              {selectedSubtopic.name}
            </Typography>
            <Typography variant="subtitle1" color="text.secondary" gutterBottom>
              Difficulty: {selectedSubtopic.difficulty}
            </Typography>

            {selectedSubtopic.articleLink && (
              <Box mb={2}>
                <Typography variant="h6" gutterBottom>
                  Article
                </Typography>
                <a
                  href={selectedSubtopic.articleLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Read Article
                </a>
              </Box>
            )}

            {selectedSubtopic.youtubeLink && (
              <Box mb={2}>
                <Typography variant="h6" gutterBottom>
                  Video Tutorial
                </Typography>
                <a
                  href={selectedSubtopic.youtubeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Watch on YouTube
                </a>
              </Box>
            )}

            {selectedSubtopic.leetcodeLink && (
              <Box mb={2}>
                <Typography variant="h6" gutterBottom>
                  Practice Problems
                </Typography>
                <a
                  href={selectedSubtopic.leetcodeLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  Solve on LeetCode
                </a>
              </Box>
            )}

            {selectedSubtopic.description && (
              <Box mt={3}>
                <Typography variant="h6" gutterBottom>
                  Description
                </Typography>
                <Typography variant="body1">
                  {selectedSubtopic.description}
                </Typography>
              </Box>
            )}
          </Box>
        ) : (
          <Box>
            <Typography variant="h4" gutterBottom>
              Welcome to DSA Tracker
            </Typography>
            <Typography variant="body1" paragraph>
              Select a topic and subtopic from the sidebar to view details and
              resources.
            </Typography>
            <Typography variant="body1">
              This application helps you track your progress in learning Data
              Structures and Algorithms. Click on any subtopic to see detailed
              information, video tutorials, and practice problems.
            </Typography>
          </Box>
        )}
      </Box>
    </Box>
  );
}

export default ResponsiveDrawer;
